.product-h1 {
  color: #fff;
}

.streaming-h2 {
  margin-left: -1rem;
  font-size: 1.4vmax;
}

.streaming-cont {
  background: rgb(0, 0, 0);
  border-radius: 1rem;
  max-width: 22.4vw;
  max-height: 88vh;
}

.parte-arriba {
  color: #fff;
  display: flex;
  border-bottom: 1px solid black;
  justify-content: space-evenly;
  padding-top: 1rem;
  padding-bottom: 0.5rem;
}

.parte-fotos {
  display: flex;
  background-image: url('./images/stranger.jpg');
  background-size: 22.4vw;
  background-repeat: no-repeat;
  box-shadow: inset 0px -130px 10px 10px rgb(0, 0, 0);
}

.cont-grid {
  margin-top: 9rem;
  margin-left: 0.5rem;
}

.grid-cont-text {
  color: #fff;
}

/* medio */

.medio {
  max-height: 65vh;
  overflow: hidden;
  overflow-y: scroll;
}

.medio::-webkit-scrollbar {
  display: none;
}

.parte-medio {
  background-color: black;
  padding: 0.5rem;
}

.boton-play {
  width: 100%;
}

.p-stranger {
  padding: 0.5rem;
  text-align: justify;
  color: #fff;
}

/* abajo */

.abajo-streaming {
  color: #fff;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 1rem;
}

/* Imagenes */

.stranger-logo {
  border-radius: 1rem;
  max-width: 5vw;
  margin-top: 8rem;
  margin-left: 0.8rem;
}

@media screen and (max-width: 600px) {
  .streaming-h2 {
    margin-left: -1rem;
    font-size: 2.1vmax;
  }
  .streaming-cont {
    background: rgb(0, 0, 0);
    border-radius: 1rem;
    max-width: 80vw;
    max-height: 88vh;
    margin-bottom: 1rem;
  }
  .parte-fotos {
    display: flex;
    background-image: url('./images/stranger.jpg');
    background-size: 80vw;
    background-repeat: no-repeat;
    box-shadow: inset 0px -100px 10px 10px rgb(0, 0, 0);
  }
  .stranger-logo {
    border-radius: 1rem;
    max-width: 20vw;
    margin-top: 8rem;
    margin-left: 0.8rem;
  }
  .abajo-streaming {
    color: #fff;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
}
